//MAIN COLORS//
:root{
    --PrimaryColor:hsl(199,100%,33%);
    --SecondaryColor:hsl(187,85%,43%);
    --gradientColor: linear-gradient(90deg, rgba(172,217,255,1) 23%, rgba(172,186,255,1) 37%);
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(201,33%,16%);
    --textColor:hsl(240,4%,36%);
    --whiteColorDeam:hsl(0,0%,93%);
    --grayText:rgb(190,190,190);
    --inputColor:rgb(239,239,239);
    --bodyColor:rgb(240,240,246);
    --cardBG:rgb(225,225,235);
  
    }
.BackToTop {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 9999;
  }
  
  .backtobutton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-size: 24px;
    background-color:var(--SecondaryColor) ;
    color: white;
    border: none;
    cursor: pointer;
  }
  //MEDIA Queries===================>
  @media screen and (max-width: 767px) {
    .BackToTop {
      bottom: 10px;
      right: 10px;
    }
  
    .backtobutton {
      width: 40px;
      height: 40px;
      font-size: 20px;
    }
  }
